const getSessionStorageKey = (key: string) => `simple-cache:${key}`;

export function getSessionCache(key: string) {
    const sessionStorageCache = sessionStorage.getItem(getSessionStorageKey(key));
    if (sessionStorageCache) {
        try {
            return JSON.parse(sessionStorageCache);
        } catch (E) {
            return null;
        }
    }
    return null;
}

export function setSessionCache(key: string, data: any) {
    sessionStorage.setItem(getSessionStorageKey(key), JSON.stringify(data));
}

export async function removeCache(key: string) {
    sessionStorage.removeItem(getSessionStorageKey(key));
}
