import React, { useState } from "react";
import { Button, Drawer, Icon, List } from "antd-mobile";
import { compose } from "@vxmind/vxhelpers";
import { hot } from "react-hot-loader";
import { withRouter } from "react-router";
import { getSessionCache, setSessionCache } from "../../../common/utils/sessionCacheHelper";
import { PartyLessionTabPanel } from "./PartyLessionTabPanel";

const { Item } = List;
const { Brief } = Item;

const DepartmentSessionKey = "PartyLessionSelectedDepartment";

const departmentList = [
    { id: 1, name: "监狱党务中心组学习 1" },
    { id: 2, name: "监狱党务中心组学习 2" },
    { id: 3, name: "监狱党务中心组学习 3" },
    { id: 4, name: "监狱党务中心组学习 4" }
];

export function PartyLessionTabContentPure(
    { history }: any
) {
    const departmentSessionData = getSessionCache(DepartmentSessionKey);
    const [isShowMenu, setIsShowMenu] = useState(!departmentSessionData);
    const [department, setDepartment] = useState(departmentSessionData);

    const handleToggleIsShowMenu = () => setIsShowMenu(!isShowMenu);
    const handleSelectDepartment = (departmentData) => {
        setSessionCache(
            DepartmentSessionKey,
            departmentData
        );
        setDepartment(departmentData);
        setIsShowMenu(false);
    };

    const menuList = (
        <List>

            {departmentList.map((item, index) => {
                return (
                    <Item
                        key={index}
                        arrow="horizontal"
                        platform="android"
                        thumb={<i className="zi zi_govDhxk"/>}
                        onClick={() => handleSelectDepartment(item)}

                    >
                        {item.name}
                    </Item>
                );
            })}


        </List>
    );

    return (
        <div>
            <img
                src={require("../images/三会一课.jpg")}
                alt="指尖党务"
                style={{ width: "100%" }}
            />

            <div style={{ position: "relative" }}>
                <Drawer
                    className="my-drawer"
                    style={{ minHeight: 600 }}
                    contentStyle={{ color: "#A6A6A6", textAlign: "center" }}
                    sidebarStyle={{ border: "1px solid #ddd" }}
                    sidebar={menuList}
                    docked={isShowMenu}
                >
                    <Button
                        className="select-department"
                        onClick={handleToggleIsShowMenu}
                    >
                        {department ? department.name : "选择部门"}
                        <Icon type="right" size="md" style={{ float: "right", marginTop: 10 }}/>
                    </Button>

                    {department ? <PartyLessionTabPanel departmentId={department.id}/> : null}
                </Drawer>
            </div>


        </div>
    );
}

export const PartyLessionTabContent = compose(
    hot(module),
    withRouter
)(PartyLessionTabContentPure);
